import {useState, useEffect} from 'react';

import {Stack, Typography, Avatar, Grid} from '@mui/material';

import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import ImageIcon from '@mui/icons-material/Image';
import WorkIcon from '@mui/icons-material/Work';
import BeachAccessIcon from '@mui/icons-material/BeachAccess';

import data from "./foods.json"

export default function FoodPage(props) {
  const {food, stds} = props;
  const [foods, setFoods] = useState();


  useEffect(() => {
    if (food == null) return;

    const foods = data.map((food2) => {
      const e = food.energy + food2.energy;
      const p = food.protein + food2.protein;
      const f = food.fat + food2.fat;
      const c = food.carb + food2.carb;

      const distance = Math.sqrt(((e - 1700) / stds['energy']) ** 2 + ((p - 60) / stds['protein']) ** 2 + ((f - 50) / stds['fat']) ** 2 + ((c - 250) / stds['carb']) ** 2)
      food2.distance = distance;
      return food2;
    });

    foods.sort((a, b) => a.distance - b.distance);

    setFoods([...foods]);

  }, []);


  return (
    <>
      <Stack spacing={1} sx={{p: 2}}>
        <Stack alignItems="center" spacing={1}>
          <Avatar src={food?.img_url} sx={{width: 60, height: 60}}/>
          <Typography variant="h6" sx={{fontWeight: "bold"}}>
            {food?.name}
          </Typography>
          <Typography sx={{fontSize: 13}}>
            B12
          </Typography>
        </Stack>
        <Typography variant="h6" sx={{fontWeight: "bold"}}>
          
        </Typography>
        <FolderList food={food}/>
        <Typography variant="h6" sx={{fontWeight: "bold"}}>
          
        </Typography>
        <Grid container>
          {foods?.map((food2) => {
            if (food == null) return;
            const e = parseInt(food.energy + food2.energy);
            const p = parseInt(food.protein + food2.protein);
            const f = parseInt(food.fat + food2.fat);
            const c = parseInt(food.carb + food2.carb);
            const eok = 1500 < e && e < 2000;
            const pok = 50 < p && p < 70;
            const fok = 40 < f && f < 60;
            const cok = 220 < c && c < 280;



            return (
              <Grid item xs={6}>
                <Stack alignItems="center" sx={{boxSizing: "border-box", p: 0.5}}>
                  <img src={food2.img_url} style={{width: '100%', borderRadius: 10}}/>
                  <Typography sx={{}}>
                    {e} {eok ? "ok" : "no"}
                  </Typography>
                  <Typography sx={{}}>
                    {p} {pok ? "ok" : "no"}
                  </Typography>
                  <Typography sx={{}}>
                    {f} {fok ? "ok" : "no"}
                  </Typography>
                  <Typography sx={{}}>
                    {c} {cok ? "ok" : "no"}
                  </Typography>
                  <Typography sx={{}}>
                    {food2.distance}
                  </Typography>
                </Stack>
              </Grid>
            )
          })}
        </Grid>
      </Stack>
    </>
  )
}

function FolderList(props) {
  const {food} = props;
  if (food == null) return;
  return (
    <List sx={{ width: '100%' }} disablePadding>
      <ListItem disableGutters secondaryAction={<Typography>{food.energy}kcal</Typography>}>
        <ListItemAvatar>
          <Avatar>
            <ImageIcon />
          </Avatar>
        </ListItemAvatar>
        <ListItemText primary="Energy" />
      </ListItem>
      <ListItem disableGutters secondaryAction={<Typography>{food.protein}g</Typography>}>
        <ListItemAvatar>
          <Avatar>
            <WorkIcon />
          </Avatar>
        </ListItemAvatar>
        <ListItemText primary="Protein" />
      </ListItem>
      <ListItem disableGutters secondaryAction={<Typography>{food.fat}g</Typography>}>
        <ListItemAvatar>
          <Avatar>
            <BeachAccessIcon />
          </Avatar>
        </ListItemAvatar>
        <ListItemText primary="Fat" />
      </ListItem>
      <ListItem disableGutters secondaryAction={<Typography>{food.carb}g</Typography>}>
        <ListItemAvatar>
          <Avatar>
            <BeachAccessIcon />
          </Avatar>
        </ListItemAvatar>
        <ListItemText primary="Carb" />
      </ListItem>
    </List>
  );
}
